<template>
  <div style="width: 100%; position: relative">
    <input @click="isShow = !isShow" v-model="filterTxt" v-bind="$attrs"/>
    <img src="../assets/img/search.svg" width="18" height="18"/>
    <transition name="slice">
      <div class="modal" v-if="isShow">
        <label v-for="item in filterList" v-bind:key="item.key">
          <input type="checkbox" :value="item.val" v-model="checkedList"/>
          <span class="font-16-normal">{{ item.val }}</span>
        </label>
      </div>
    </transition>
  </div>
</template>
<script lang="ts">
  import {
    Component, Model, Prop, Vue,
  } from 'vue-property-decorator';
  import { IListItem } from '@/types/interface';

  @Component
  export default class BaseSearchSelection extends Vue {
    @Prop({ default: () => [] }) list!: IListItem[] | [];
    @Model('change', { default: () => [] }) readonly checked!: [string];

    filterTxt = '';
    isShow = false;

    get filterList() {
      if (this.filterTxt === '') return this.list;
      return this.list.filter((item: IListItem) => item.val.indexOf(this.filterTxt) >= 0);
    }

    get checkedList() {
      return this.checked;
    }

    set checkedList(list) {
      this.$emit('change', list);
    }
  }
</script>
<style scoped>
  div > input:first-child {
    width: 100%;
    height: 42px;
    border: 1px solid #CBCBCB;
    box-sizing: border-box;
    border-radius: 8px;
    padding: 10px;
  }

  input + img {
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translate(0, -50%);
  }

  input:focus {
    outline: unset!important;
  }

  .modal {
    position: absolute;
    left: 0;
    top: 42px;
    z-index: 1;
    width: 100%;
    background: #FFFFFF;
    border: 1px solid #E9E9E8;
    box-sizing: border-box;
    border-radius: 8px;
    overflow-y: auto;
    padding: 4px 16px;
  }

  .slice-enter-active, .slice-leave-active {
    transition: all .5s;
    max-height: 280px;
  }

  .slice-enter, .slice-leave-to {
    max-height: 0;
  }

  label {
    display: flex;
    width: 100%;
    height: 42px;
    align-items: center;
  }

  input[type="checkbox"] {
    width: 16px;
    height: 16px;
    margin: 0 10px 0 0;
  }
</style>
